<template>
  <f7-page>
    <div class="interactive">
      <f7-segmented>
        <f7-button :class="{ 'button-active': isShow }" @click="filterShow(true)">已顯示</f7-button>
        <f7-button :class="{ 'button-active': !isShow }" @click="filterShow(false)">已關閉</f7-button>
      </f7-segmented>
      <f7-page
        infinite
        :infinite-distance="50"
        :infinite-preloader="showPreloader"
        @infinite="loadMore"
      >
        <transition name="fade">
          <ul v-if="listShow">
            <li v-for="(item, index) in filterUploadPhotoList" :key="index">
              <!-- 照片 -->
              <div class="photo" :style="{ backgroundImage: 'url(' + item.url + ')' }"></div>
              <!-- 區塊 -->
              <div class="interactive-area">
                <!-- 內容 -->
                <div class="content">
                  <h1 v-text="item.name"></h1>
                  <pre v-text="item.message"></pre>
                </div>
                <!-- 按鈕 -->
                <div class="buttons">
                  <a href="#" :class="isShow? 'show on' : 'show off'" @click="setOnOff(item)"></a>
                  <a :href="'/editphoto/' + item.id + '/'" class="rotate"></a>
                </div>
              </div>
            </li>
          </ul>
        </transition>
      </f7-page>
    </div>
  </f7-page>
</template>

<script>
export default {
  name: "Interactive",
  data() {
    return {
      // 上傳照片列表
      uploadPhotoList: [
        {
          id: 1,
          url: require("@/assets/album/photo1.jpg"),
          name: "唯美浪漫",
          message: "祝福新人百年好\r\n永浴愛河~~",
          isShow: true
        },
        {
          id: 2,
          url: require("@/assets/album/photo2.jpg"),
          name: "海上太空漫步",
          message: "夕陽下的海岸險得格外漂亮、美麗呢",
          isShow: true
        },
        {
          id: 3,
          url: require("@/assets/album/photo3.jpg"),
          name: "詭異的花籃",
          message: "椅子旁只有這一個花籃",
          isShow: true
        },
        {
          id: 4,
          url: require("@/assets/album/photo4.jpg"),
          name: "甜蜜蜜的一對新人",
          message: "浪漫到不要不要的，很羨慕呢\r\n~~~",
          isShow: true
        },
        {
          id: 5,
          url: require("@/assets/album/photo1.jpg"),
          name: "唯美浪漫",
          message: "祝福新人百年好\r\n永浴愛河~~",
          isShow: true
        },
        {
          id: 6,
          url: require("@/assets/album/photo2.jpg"),
          name: "海上太空漫步",
          message: "夕陽下的海岸險得格外漂亮、美麗呢",
          isShow: false
        },
        {
          id: 7,
          url: require("@/assets/album/photo3.jpg"),
          name: "詭異的花籃",
          message: "椅子旁只有這一個花籃",
          isShow: true
        },
        {
          id: 8,
          url: require("@/assets/album/photo4.jpg"),
          name: "甜蜜蜜的一對新人",
          message: "浪漫到不要不要的，很羨慕呢\r\n~~~",
          isShow: false
        }
      ],
      // 過濾顯示
      isShow: true,
      // 列表顯示
      listShow: true,
      // 允許滾動
      allowInfinite: true,
      // 顯示Preloader
      showPreloader: false
    };
  },
  methods: {
    // 篩選顯示
    filterShow: function(isShow) {
      var self = this;
      var speed = 200;

      self.isShow = isShow;
      self.listShow = false;
      self.allowInfinite = false;
      self.uploadPhotoList = self.uploadPhotoList.slice(0, 8);
      setTimeout(() => {
        self.listShow = true;
        self.allowInfinite = true;
        self.showPreloader = false;
      }, speed);
    },
    // 開關
    setOnOff: function(obj) {
      obj.isShow = !this.isShow;
    },
    // 滾動加載
    loadMore: function() {
      var self = this;
      var maxLimit = 30;
      var speed = 500;

      if (!self.allowInfinite) return;
      self.allowInfinite = false;
      self.showPreloader = true;
      setTimeout(() => {
        if (self.uploadPhotoList.length >= maxLimit) {
          self.showPreloader = false;
          return;
        }
        self.uploadPhotoList = self.uploadPhotoList.concat(
          self.uploadPhotoList.slice(0, 8)
        );
        self.allowInfinite = true;
      }, speed);
    }
  },
  computed: {
    // 過濾上傳照片列表
    filterUploadPhotoList() {
      var self = this;

      return self.uploadPhotoList.filter(function(item) {
        return item.isShow == self.isShow;
      });
    }
  },
  created() {},
  mounted() {}
};
</script>

<style scoped lang="scss" src="../scss/interactive.scss"></style>