<template>
  <f7-page
    infinite
    :infinite-distance="50"
    :infinite-preloader="showPreloader"
    @infinite="loadMore"
  >
    <div class="album">
      <!-- Photo Browser -->
      <f7-photo-browser
        ref="pb"
        :photos="photoList"
        theme="dark"
        type="standalone"
        :exposition="true"
        :lazyLoading="true"
        backLinkText="Close"
      ></f7-photo-browser>

      <f7-block-title>婚紗藝廊</f7-block-title>
      <p class="album-description">熊大與麗莎的奇幻精彩婚禮歷程</p>
      <f7-block>
        <f7-row no-gap>
          <f7-col
            v-for="(item, index) in photoList"
            :key="index"
            width="33"
            class="album-item"
            :style="{ backgroundImage: 'url(' + item.url + ')' }"
            @click="openPhotoBrowser(index)"
          ></f7-col>
        </f7-row>
      </f7-block>
    </div>
  </f7-page>
</template>

<script>
export default {
  name: "Album",
  data() {
    return {
      // 照片列表
      photoList: [
        {
          url: require("@/assets/album/photo1.jpg"),
          caption: "Picture 1"
        },
        {
          url: require("@/assets/album/photo2.jpg"),
          caption: "Picture 2"
        },
        {
          url: require("@/assets/album/photo3.jpg"),
          caption: "Picture 3"
        },
        {
          url: require("@/assets/album/photo4.jpg"),
          caption: "Picture 4"
        },
        {
          url: require("@/assets/album/photo5.jpg"),
          caption: "Picture 5"
        },
        {
          url: require("@/assets/album/photo6.jpg"),
          caption: "Picture 6"
        },
        {
          url: require("@/assets/album/photo1.jpg"),
          caption: "Picture 7"
        },
        {
          url: require("@/assets/album/photo2.jpg"),
          caption: "Picture 8"
        },
        {
          url: require("@/assets/album/photo3.jpg"),
          caption: "Picture 9"
        },
        {
          url: require("@/assets/album/photo4.jpg"),
          caption: "Picture 10"
        },
        {
          url: require("@/assets/album/photo5.jpg"),
          caption: "Picture 11"
        },
        {
          url: require("@/assets/album/photo6.jpg"),
          caption: "Picture 12"
        },
        {
          url: require("@/assets/album/photo1.jpg"),
          caption: "Picture 13"
        },
        {
          url: require("@/assets/album/photo2.jpg"),
          caption: "Picture 14"
        },
        {
          url: require("@/assets/album/photo3.jpg"),
          caption: "Picture 15"
        }
      ],
      // 允許滾動
      allowInfinite: true,
      // 顯示Preloader
      showPreloader: false
    };
  },
  methods: {
    // 開啟照片瀏覽器
    openPhotoBrowser: function(index) {
      this.$refs.pb.open(index);
    },
    // 滾動加載
    loadMore: function() {
      var self = this;
      var maxLimit = 60;
      var speed = 500;

      if (!self.allowInfinite) return;
      self.allowInfinite = false;
      self.showPreloader = true;
      setTimeout(() => {
        if (self.photoList.length >= maxLimit) {
          self.showPreloader = false;
          return;
        }
        self.photoList = self.photoList.concat(self.photoList.slice(0, 15));
        self.$refs.pb.f7PhotoBrowser.params.photos = self.photoList;
        self.allowInfinite = true;
      }, speed);
    }
  },
  created() {},
  mounted() {}
};
</script>

