// Import Vue
import Vue from 'vue';
// Import store
import store from './store';

// Import F7
import Framework7 from 'framework7/framework7.esm.bundle.js';

// Import F7 Vue Plugin
import Framework7Vue from 'framework7-vue/framework7-vue.esm.bundle.js';

// Import F7 Welcomescreen
import welcomescreen from 'f7-welcomescreen';

// Import F7 Css
import FrameworkCss from 'framework7/css/framework7.css';

// Import Icons and App Custom Css
import IconsCss from './css/icons.css';
import AppCss from './scss/app.scss';

// Import App Component
import App from './App.vue';

Vue.config.productionTip = false

// Init F7 Vue Plugin
Framework7.use(Framework7Vue)
Framework7.use(welcomescreen)

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')